<template>
  <div class="main-div">
    <div class="h-35 d-inline-block" style="width: 100%">
      <h5 class="align-left pad-left">Leave History Details :   {{this.empId}} - {{this.empName}}</h5>
    </div>
    <b-row>
      <div >
        <b-container fluid>
          <b-table
            :sticky-header=true
            class="table table-bordered table-sm"
            selected-variant="active"
            :items="leaveHistoryList"
            :fields="fields"
            :current-page="currentPage"
            :filter="filter"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            :hover="true"
            v-model="visibleRows"
            selectable
            ref="selectableTable"
          >
          <template #cell(description)="data">
            {{ (data.item.annualLeave > 0 || data.item.sickLeave > 0  || data.item.casualLeave > 0 )? 'Earned' : 'used'}}
          </template>
            <template v-slot:cell(selected)="{ rowSelected }">
              <template v-if="rowSelected">
                <span class="sr-only">Selected</span>
              </template>
              <template v-else>
                <span class="sr-only">Not selected</span>
              </template>
            </template>
            <template slot="bottom-row" class="row-background">
              <td class="col-left">
                <h4><b-badge>Total</b-badge></h4>
              </td>
              <td></td>
              <td></td>
              <td></td>
              <td class="text-right">{{ t1.toFixed(3) | formatter1 }}</td>
            </template>
            <template slot="bottom-row">
              <td class="text-right">{{ t2.toFixed(3) | formatter1 }}</td>
            </template>
            <template slot="bottom-row">
              <td class="text-right">{{ t3.toFixed(3) | formatter1 }}</td>
            </template>
          </b-table>
          <!-- <b-pagination
            v-model="currentPage"
            :total-rows="totalRows"
            :per-page="perPage"
            size="sm"
            class="my-0"
          ></b-pagination> -->
        </b-container>
      </div>
    </b-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import moment from 'moment'

export default {
  props: {
    empId: {},
    empName: {}
  },
  data () {
    return {
      visibleRows: [],
      currentPage: 1,
      perPage: 5,
      // totalRows: items.length,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      filter: '',
      sortDirection: 'asc',
      // searchEmp: {
      //   attr1: { name: 'id', label: 'Employee Code' },
      //   attr2: { name: 'empName', label: 'Employee Name' },
      //   attr3: { name: 'cprNo', label: 'Cpr No' },
      //   attr4: { name: 'workDesignation', label: 'Work designation' }
      // },
      leaveHistoryList: [],
      fields: [
        {
          key: 'id',
          label: 'Transaction Id',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'dateFrom',
          label: 'Leave From Date',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        {
          key: 'dateTo',
          label: 'Leave Date to',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          formatter: 'formatData'
        },
        { key: 'description', label: 'Description' },
        {
          key: 'annualLeave',
          label: 'Annual Leave',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
        },

        {
          key: 'sickLeave',
          label: 'Sick Leave',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
        },
        {
          key: 'casualLeave',
          label: 'Other Leave',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
          // formatter (value) {
          //   return moment(value).format('DD-MM-YYYY')
          // }
        },
        {
          key: 'adjLeave',
          label: 'Adjusted Leave',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-right',
          formatter: 'formatData'
          // formatter (value) {
          //   return moment(value).format('DD-MM-YYYY')
          // }
        }
      ]
    }
  },
  mounted () {
    this.fetchLeaveHistoryList(this.empId)
  },
  components: {
  },
  computed: {
    ...mapGetters([
      'message',
      'employeeList',
      'fetchStartRec',
      'employee',
      'totalRows'
    ]),
    t1 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.annualLeave)
      }, 0.0)
    },
    t2 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.sickLeave)
      }, 0.0)
    },
    t3 () {
      return this.visibleRows.reduce((accum, item) => {
        return accum + parseFloat(item.casualLeave)
      }, 0.0)
    }
  },
  filters: {
    formatter1: function (value) {
      return new Intl.NumberFormat(undefined, {
        minimumFractionDigits: 2
      }).format(value)
    }
  },
  methods: {
    ...mapActions(['defaultState']),
    fetchLeaveHistoryList (x) {
      const url =
        process.env.VUE_APP_API_URL + `/employee/leaveTransactionList/${x}`
      // console.log('url:' + url)
      fetch(url)
        .then(res => res.json())
        .then(data => {
          // console.log('Output: ', data)
          this.leaveHistoryList = data
        })
        .catch(err => console.error(err))
    },
    formatData (value, x) {
      if (!value) return ''
      if (x.includes('date')) {
        // console.log('col name includes date')
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else if (x.includes('Date')) {
        value = value.toString()
        return moment(String(value)).format('DD-MM-YYYY')
      } else {
        // console.log('not formated')
        return value
      }
    }
  },
  watch: {
    empId: {
      handler: function () {
        this.fetchLeaveHistoryList(this.empId)
      }
    }
  }
}
</script>

<style scoped>
.main-div {
  width: 100%;
  margin-left: 25px;
  margin-right: 25px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: medium;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}

#b-col {
  text-align: left;
}
.col-middle {
  /* vertical-align: bottom; */
  margin-left: -90px;
  padding-top: 25px;
  padding-left: 0px;
  -webkit-appearance: none;
}
.textBoxWidth {
  width: 160px;
}
.cellColor {
  background-color: #d9dcdf;
}
.calculated-value {
  background-color: #d9dcdf;
}
.input-required {
  background-color: rgb(245, 242, 212);
}
.page-head {
  font-weight: bold;
}
table, th, td {
  border-collapse: collapse;
}
th, td {
  padding: 25px;
}
tr:nth-child(even) {
  background-color: rgba(150, 212, 212, 0.4);
}

/* th:nth-child(even),td:nth-child(even) {
  background-color: rgba(150, 212, 212, 0.4);
} */
</style>
