<template>
  <div>
    <!-- {{ this.empLeaveSummary }} -->
    <!-- {{this.msg}} -->
    <div class="h-35 d-inline-block" style="width: 100%">
      <h2 class="page-head">Add Leave</h2>
    </div>
    <div class="main-div">
      <hr />
      <b-row class="mt-1">
        <b-col cols="10">
          <b-row class="mt-2">
            <b-col cols="2" offset="1" class="col-left">
              <label for="input-code">Trn. Id:</label>
            </b-col>
            <b-col cols="2" class="col-left">{{
              leaveTransaction.trnId
            }}</b-col>
          </b-row>
          <b-row class="mt-2">
            <b-col cols="2" offset="1" class="col-left">
              <label for="input-code">Code:</label>
            </b-col>
            <b-col cols="2">
              <b-form-group>
                <b-input-group>
                  <b-form-input
                    class="col-right form-control form-control-sm"
                    v-model="leaveTransaction.empCode"
                    placeholder="Employee code"
                    @change="getEmployee"
                    v-b-tooltip.hover
                    title="Enter employee code"
                  ></b-form-input>
                  <template v-slot:append>
                    <searchSm
                      :column1Key="searchEmp.attr1.name"
                      :column1label="searchEmp.attr1.label"
                      :column2Key="searchEmp.attr2.name"
                      :column2label="searchEmp.attr2.label"
                      :fetchItem="fetchLTEmployee1"
                      @fetchD="fetchEmployeeList"
                      :items="employeeList"
                      :totalRowsT="totalRows"
                    />
                  </template>
                </b-input-group>
              </b-form-group>
            </b-col>
            <b-col cols="5" class="col-left">{{
              leaveTransaction.empName
            }}</b-col>
          </b-row>
          <b-row class="mt-2">
            <b-col cols="2" offset="1" class="col-left">
              <label for="input-code">Leave Type:</label>
            </b-col>
            <b-form-radio-group
              v-model="selectedLeaveType"
              :options="options"
              class="mb-3"
              value-field="item"
              text-field="name"
              disabled-field="notEnabled"
            ></b-form-radio-group>
            <b-form-checkbox id="chkLA" v-model="status">
              Leave Adjustment
            </b-form-checkbox>
          </b-row>
          <b-row class="mt-2">
            <b-col cols="2" offset="1" class="col-left">
              <label for="input-code">Nature:</label>
            </b-col>
            <b-col cols="4">
              <b-form-input
                class="form-control form-control-sm"
                id="input-id"
                v-model="leaveTransaction.leaveNature"
                placeholder="Nature of leave"
              ></b-form-input>
            </b-col>
          </b-row>
          <b-row class="mt-2">
            <b-col cols="2" offset="1" class="col-left">
              <label for="input-date"> From Date:</label>
            </b-col>
            <b-col cols="2">
              <datepicker
                placeholder="Select Date"
                v-model="leaveTransaction.dateFrom"
                input-class="form-control form-control-sm styleLine"
              ></datepicker>
            </b-col>
            <b-col cols="1" class="col-left">
              <label for="input-date"> To Date:</label>
            </b-col>
            <b-col cols="2">
              <datepicker
                placeholder="Select Date"
                v-model="leaveTransaction.dateTo"
                input-class="form-control form-control-sm styleLine"
                @selected="validateDate"
              ></datepicker>
            </b-col>
          </b-row>
          <!-- <b-row class="mt-2">
            <b-col cols="1" offset="1" class="col-left">
              <label for="chkApproved"> Approved:</label>
            </b-col>
            <b-col cols="2" offset="1">
              <b-form-checkbox
                v-model="leaveTransaction.approvalStatus"
                name="chkApproved"
                value="1"
                unchecked-value="0"
                switch
              >
              </b-form-checkbox>
            </b-col>
          </b-row> -->
          <b-row class="mt-2">
            <b-col cols="1" offset="1" class="col-left">
              <label for="input-remarks"> Remarks:</label>
            </b-col>
            <b-col cols="6" offset="1">
              <b-form-textarea
                class="form-control form-control-sm"
                id="input-remarks"
                v-model="leaveTransaction.remarks"
                placeholder="Remarks"
                rows="3"
                max-rows="6"
              ></b-form-textarea>
            </b-col>
          </b-row>
          <b-row class="mt-2">
            <b-col cols="2" offset="1" class="col-left">
              <label for="input-approvalDate"> Application Date:</label>
            </b-col>
            <b-col cols="2">
              <datepicker
                placeholder="Select Date"
                v-model="leaveTransaction.applicationDate"
                input-class="form-control form-control-sm styleLine"
              ></datepicker>
            </b-col>
            <b-col cols="2" offset="1" class="col-left">
              <label for="total">Total days:</label>
            </b-col>
            <b-col cols="1" style="">
              <p
                class="
                  text-right
                  calculated-value
                  form-control form-control-sm
                  styleLine
                  col-right
                "
              >
                {{ total }}
              </p>
            </b-col>
          </b-row>
        </b-col>
        <b-col cols="2">
          <b-row>
            <b-card
              header="Leave Balance information"
              class="text-center"
              header-tag="header"
            >
              <b-card-text>
                <b-row>
                  <table>
                    <tr>
                      <td>Annual Leave</td>
                      <td>{{ this.empLeaveSummary.annualLeave }}</td>
                    </tr>
                    <tr>
                      <td>Sick Leave</td>
                      <td>{{ this.empLeaveSummary.sickLeave }}</td>
                    </tr>
                    <tr>
                      <td>Casual Leave</td>
                      <td>{{ this.empLeaveSummary.casualLeave }}</td>
                    </tr>
                  </table>
                </b-row>
              </b-card-text>
              <b-button href="#" variant="secondary"   @click="showDetails(empLeaveSummary)">Show details</b-button>
              <!-- <b-button size="sm" v-b-modal="'bv-modal-leaveHistoryDetail'" empId="empLeaveSummary.id" >
              </b-button> -->
            </b-card>
          </b-row>
        </b-col>
      </b-row>
      <hr />
      <b-row align-h="center">
        <b-button variant="dark" class="mr-1" @click="save()"
          >Submit</b-button
        >
        <b-button
          variant="outline-secondary"
          class="mr-1"
          @click="defaultState()"
        >
          Clear
        </b-button>
        <b-button
          variant="secondary"
          class="mr-1"
          @click="setLeaveFormDisplay(), houseKeeping()"
          >Close</b-button
        >
      </b-row>
      <br />
    </div>
    <b-modal ref="my-modal" size="xl" >
        <leaveDetail :empId = "this.leaveTransaction.empCode"/>
  </b-modal>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import { mapActions, mapGetters } from 'vuex'
import searchSm from '../components/SearchSm.vue'
import leaveDetail from './LeaveHistoryDetail.vue'
import axios from 'axios'
// import moment from 'moment'
export default {
  data () {
    return {
      msg: {},
      empLeaveSummary: {},
      employeeLeave: {},
      selectedLeaveType: 'SL',
      date1: new Date(),
      date2: new Date(),
      status: '',
      nature: '',
      options: [
        { item: 'SL', name: 'Sick Leave' },
        { item: 'AL', name: 'Annual Leave' },
        { item: 'OL', name: 'Other Leave' }
      ],
      searchEmp: {
        attr1: { name: 'id', label: 'Employee Code' },
        attr2: { name: 'empName', label: 'Employee Name' }
      }
    }
  },
  components: {
    Datepicker,
    searchSm,
    leaveDetail
  },
  computed: {
    ...mapGetters([
      'leaveTransaction',
      'employeeList',
      'totalRows'
    ]),
    total: function () {
      // var start = moment(this.leaveTransaction.dateFrom.toLocaleDateString('en-us'))
      // var end = moment(this.leaveTransaction.dateTo.toLocaleDateString('en-us'))
      // end.diff(start, 'days')
      // return end.diff(start, 'days')
      const d1 = this.leaveTransaction.dateFrom
      const d2 = this.leaveTransaction.dateTo
      d1.setHours(0, 0, 0, 0)
      d2.setHours(0, 0, 0, 0)
      const dateOne = new Date(d1)
      const dateTwo = new Date(d2)
      // const time = Math.abs(dateTwo - dateOne)
      const time = (dateTwo - dateOne)
      // return Math.ceil(time / (1000 * 60 * 60 * 24))
      if (dateTwo - dateOne < 0) {
        return Math.ceil(time / (1000 * 60 * 60 * 24)) - 1
      } else {
        return Math.ceil(time / (1000 * 60 * 60 * 24)) === 0 ? 1 : Math.ceil(time / (1000 * 60 * 60 * 24)) + 1
      }
    }
  },
  methods: {
    ...mapActions([
      'saveLeaveTransaction',
      'fetchEmployeeList',
      'fetchLTEmployee',
      'defaultStateEmpLeave',
      'setLeaveFormDisplay'
    ]),
    validateDate () {
      // alert('date verify:' + (new Date(this.leaveTransaction.dateTo).getTime) >= (new Date(this.leaveTransaction.dateFrom).getTime))
      const date1 = new Date(this.leaveTransaction.dateTo).getTime()
      const date2 = new Date(this.leaveTransaction.dateFrom).getTime()
      console.log(date1)
      console.log(date2)
      console.log(date2 >= date1)
    },
    async fetchLTEmployee1 (x) {
      await this.fetchLTEmployee(x)
      await this.fetchEmployeeLeaveSummary(x)
    },
    async getEmployee () {
      await this.fetchLTEmployee(this.leaveTransaction.empCode)
      await this.fetchEmployeeLeaveSummary(this.leaveTransaction.empCode)
      if (this.message.length !== 0) {
        this.$toasted.show(this.message, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
    },
    calculateDays () {
      // var d1 = document.getElementById('d1').value
      // var d2 = document.getElementById('d2').value
      // const dateOne = new Date(d1)
      // const dateTwo = new Date(d2)
      // const time = Math.abs(dateTwo - dateOne)
      // const days = Math.ceil(time / (1000 * 60 * 60 * 24))
    },
    // findDateDifference (d1, d2) {
    //   // var diffDays = d2.diff(d1, 'days')
    //   // alert(diffDays)
    //   var a = moment(d1, 'M/D/YYYY')
    //   var b = moment(d2, 'M/D/YYYY')
    //   var diffDays = b.diff(a, 'days')
    //   // alert(diffDays)
    // },
    checkForm: function (e) {
      console.log('checking form data')
      this.errors = []
      if (!this.leaveTransaction.empCode) {
        this.errors.push('Please select employee.')
      }
      if (!this.leaveTransaction.leaveNature) {
        this.errors.push('Leave Nature required.')
      }
      if (!this.leaveTransaction.leaveType) {
        this.errors.push('Leave Type Required.')
      }
      if (!this.leaveTransaction.dateFrom) {
        this.errors.push('Leave date from Required.')
      }
      if (!this.leaveTransaction.dateTo) {
        this.errors.push('Leave date to Required.')
      }
      if (!this.errors.length) {
        return true
      } else {
        this.errors.unshift(
          'Fields highlighted in yellow are required, please enter following ..'
        )
      }
    },
    houseKeeping () {
      this.errors = []
    },
    async save () {
      // console.log('save')
      this.leaveTransaction.leaveType = this.selectedLeaveType
      this.leaveTransaction.noOfWorkingdays = this.total
      this.leaveTransaction.adjLeave = this.status === true ? 1 : 0
      if (this.status === true) {
        this.leaveTransaction.noOfWorkingdays = this.leaveTransaction.noOfWorkingdays * -1
      }
      this.checkForm()
      if (!this.errors.length) {
        if (this.leaveTransaction.id === 0) {
          console.log('saving')
          await this.saveLeaveTransaction(this.leaveTransaction)
          await this.defaultState()
        }
        this.$toasted.show(this.message, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      } else {
        // console.log('Show errors!!')
        this.$toasted.show(this.errors, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
      this.houseKeeping()
    },
    defaultState () {
      this.empLeaveSummary = {
        annualLeave: 0,
        casualLeave: 0,
        id: 0,
        sickLeave: 0
      }
      this.defaultStateEmpLeave()
    },
    async fetchEmployeeLeaveSummary (id) {
      // alert('fetchEmployeeLeaveSummary' + id)
      const url = process.env.VUE_APP_API_URL + `/employee/leaveSummary/${id}`
      await axios
        .get(url)
        .then((response) => {
          return response
        })
        .then((response) => {
          this.empLeaveSummary = response.data
          this.msg = {}
        })
        .catch((error) => {
          console.log(error.response.data)
          this.empLeaveSummary = {
            annualLeave: 0,
            casualLeave: 0,
            id: 0,
            sickLeave: 0
          }
          this.msg = error.response.data.errorMessage
        })
    },
    showDetails () {
      this.$refs['my-modal'].show()
    }
  }
}
</script>

<style scoped>
.main-div {
  width: 95%;
  margin-left: 25px;
  margin-right: 25px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: medium;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}

#b-col {
  text-align: left;
}
.col-middle {
  /* vertical-align: bottom; */
  margin-left: -90px;
  padding-top: 25px;
  padding-left: 0px;
  -webkit-appearance: none;
}
.textBoxWidth {
  width: 160px;
}
.cellColor {
  background-color: #d9dcdf;
}
.calculated-value {
  background-color: #d9dcdf;
}
.input-required {
  background-color: rgb(245, 242, 212);
}
.page-head {
  font-weight: bold;
}
table, th, td {
  border-collapse: collapse;
}
th, td {
  padding: 25px;
}
tr:nth-child(even) {
  background-color: rgba(150, 212, 212, 0.4);
}

/* th:nth-child(even),td:nth-child(even) {
  background-color: rgba(150, 212, 212, 0.4);
} */
</style>
